.content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
}

.count{
    width: 50px;
    height: 50px;
    background-color: black;
    color: aqua;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
}
.btn{
    width: 60px;
    height: 30px;
    border-radius: 5px;
    border: none;
    background-color: aqua;
    box-shadow: 1px 1px 5px black;
}