.navbar{
    height: 100px;
    background-color: rgba(19, 19, 19, 0.900);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}

.nav-logo{
    color: white;
}
.nav-link{
    display: flex;
    flex-direction: column;
}
.nav-link a{
    margin: 0 10px;
    color: white;
    text-decoration: none;
}